<template>
  <!-- 
    getActivityProductListById 限时折扣活动中的所有商品
   -->
  <el-dialog :append-to-body="true" title="新增商品列表" :visible.sync="discountCommodityShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="商品名称：">
            <el-input style="width: 203px" v-model="searchList.productName" clearable placeholder="商品名称" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="商品类目：">
            <el-cascader
              v-model="searchList.shopCategoryIdArr"
              :options="category"
              :props="{ checkStrictly: true }"
              clearable></el-cascader>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table 
        ref="multipleTable" 
        :data="list" 
        tooltip-effect="dark" 
        style="width: 100%" 
        @selection-change="handleSelectionChange"
        :row-class-name="getRowClassName">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-table :data="props.row.list" border style="width: 100%">
              <el-table-column prop="skuName" label="规格"></el-table-column>
              <el-table-column label="成本价（元）">
                <template slot-scope="scope">￥{{ scope.row.costPrice}}</template>
              </el-table-column>
              <el-table-column label="商品原价（元）">
                <template slot-scope="scope">￥{{ scope.row.price}}</template>
              </el-table-column>
              <el-table-column prop="finallyPrice" label="折后价格（元）">
                <template slot-scope="scope">￥{{ scope.row.finallyPrice }}</template>
              </el-table-column>
              <el-table-column prop="skuStock" label="库存"></el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column label="商品图片" prop="productName" width="140">
          <template slot-scope="scope">
            <el-popover placement="right" title trigger="hover">
              <el-image
                slot="reference"
                :src="scope.row.productImgUrl"
                :alt="scope.row.productImgUrl"
                style="max-height: 40px;max-width: 40px"
              ></el-image>
              <el-image :src="scope.row.productImgUrl" style="max-height: 350px;max-width: 350px"></el-image>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="商品名称" prop="productName" width="300"></el-table-column>
        <el-table-column label="商品已售" prop="productFinallyTotal"></el-table-column>
        <el-table-column label="成本价（元）">
          <template slot-scope="scope">{{ scope.row.costPrice ? '￥'+scope.row.costPrice : '-' }}</template>
        </el-table-column>
        <el-table-column label="商品售价（元）">
          <template slot-scope="scope">￥{{ scope.row.minPrice }}</template>
        </el-table-column>
        <el-table-column label="商品折扣" prop="discount">
          <template slot-scope="scope">{{ scope.row.discount}}%</template>
        </el-table-column>
        <el-table-column label="折后价格">
          <template slot-scope="scope">￥{{ scope.row.finallyPrice == 0 ? scope.row.minPrice : scope.row.finallyPrice}}</template>
        </el-table-column>
        <el-table-column label="商品库存" prop="productStock"></el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
      <el-button type="primary" @click="sub">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getProductCategoryShopAllList } from '@/api/commodity/commodityClassify'
import { getActivityProductListById } from '@/api/marketManage/discount'
export default {
  props: ["discountCommodityIds","discountCommodityShow","discountId"],
  watch:{
    discountCommodityShow(newvalue,oldValue){
      if(newvalue){
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        shopCategoryIdArr:[],
        pageNum:1,
        pageSize:5
      },
      category:[],
      list:[],
      listTotal:0,
      multipleSelection:[],           // 勾选的数据
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
  },
  methods:{
    sub(){
      console.log('multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择商品后再试');
        return;
      }
      this.$emit('discountCommodityFun',this.multipleSelection)
      this.$refs.multipleTable.clearSelection();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.productName = ''
      this.searchList.shopCategoryIdArr = []
      this.getData();
    },
    // 列表 - 获取数据
    getData(){
      this.fromLoading = true
      this.searchList.activityId = this.discountId;
      this.searchList.ids = this.discountCommodityIds || []
      this.searchList.shopCategoryPid = this.searchList.shopCategoryIdArr[0] || ''
      this.searchList.shopCategoryId = this.searchList.shopCategoryIdArr[1] || ''
      console.log('请求的数据',this.searchList)
      getActivityProductListById(this.searchList).then(res=>{
        console.log('数据列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
          this.getCategory()
        }
        this.fromLoading = false
      })
    },
    getRowClassName({row, rowIndex}){
      if (row.skuType == 0) {
        return 'row-expand-cover';
      }
    },
    // 获取全部分类
    getCategory(){
      getProductCategoryShopAllList(this.shop.shopId).then(res=>{
        console.log('获取店铺分类',res)
        this.category = res.data
      })
    },
    close(){
      this.$emit('discountCommodityClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style>
  .row-expand-cover .el-table__expand-icon{visibility:hidden;}
</style>