<template>
  <div class="itemBox">
    <div>
      <p class="title">限时折扣</p>
      <el-form ref="form" :model="itemData" size="mini">
        <el-form-item label="选择活动" class="navigation">
          <el-button v-if="!itemData.activityId" @click="discountListShow = true">选择活动</el-button>
          <div class="flex-row-start" v-else>
            <div>{{itemData.activityName}}</div>
            <el-button type="text" style="margin-left:20px;" @click="discountListShow = true">修改</el-button>
          </div>
        </el-form-item>
        <el-form-item v-if="itemData.activityId" label="选择商品" class="navigation">
          <el-button style="margin-bottom:10px;" @click="discountCommodityShow = true">添加商品</el-button>
          <ul class="goodsUploadList" v-show="itemData.goodsData.length!=0">
            <li
              v-for="(item,index) in itemData.goodsData"
              :key="index"
              @mouseover="mouseOver(index)"
              @mouseleave="mouseLeave"
            >
              <img :src="item.productImgUrl" alt />
              <img
                class="delBtn"
                :class="hoverIndex==index?'delBtnShow':''"
                src="@/assets/decorateBox/goods/close.png"
                @click="delGoods(index)"
                alt
              />
            </li>
          </ul>
        </el-form-item>
        <el-form-item label="列表样式" class="navigation">
          <el-radio-group v-model="itemData.listType" @change="listSelect">
            <el-radio :label="0">大图样式</el-radio>
            <el-radio :label="1">小图样式</el-radio>
            <el-radio :label="2">一行三个</el-radio>
            <el-radio :label="3">详细列表</el-radio>
            <el-radio :label="4">横向滑动</el-radio>
            <el-radio :label="5">一大两小</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="商品样式" class="showType">
          <el-radio-group v-model="itemData.chamfering" @change="showType(0)">
            <span class="showClass">卡片倒角</span>
            <el-radio :label="0">直角</el-radio>
            <el-radio :label="1">圆角</el-radio>
          </el-radio-group>
          <el-radio-group v-model="itemData.projection" @change="showType(1)">
            <span class="showClass">卡片投影</span>
            <el-radio :label="0">无投影</el-radio>
            <el-radio :label="1">有投影</el-radio>
          </el-radio-group>
          <el-radio-group v-model="itemData.fill" @change="showType(2)">
            <span class="showClass">图片填充</span>
            <el-radio :label="0">填充</el-radio>
            <el-radio :label="1">周边留白</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="itemData.listType != 3" label="文本样式" class="showType">
          <el-radio-group v-model="itemData.titleAlign">
            <span class="showClass">文本对齐</span>
            <el-radio :label="0">左对齐</el-radio>
            <el-radio :label="1">居中对齐</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="显示内容">
          <el-checkbox disabled v-model="trueDefault">商品名称</el-checkbox>
          <el-checkbox disabled v-model="trueDefault">商品价格</el-checkbox>
          <el-checkbox v-model="itemData.showTime">抢购倒计时</el-checkbox>
          <el-checkbox  v-if="itemData.listType != 2 && itemData.listType != 4" v-model="itemData.showTimeLine">抢购进度条</el-checkbox>
          <el-checkbox v-model="itemData.showLast">剩余库存</el-checkbox>
          <el-checkbox v-model="itemData.showLinePrice">划线原价</el-checkbox>
          <el-checkbox v-model="itemData.showBuyBtn" @change="changeShow(0)">购买按钮</el-checkbox>
          <el-radio-group v-model="itemData.buyBtn" class="buyStyle" @change="selsecBtn">
            <el-radio :label="0" :disabled="radioType">样式1</el-radio>
            <el-radio :label="1" :disabled="radioType">样式2</el-radio>
            <el-radio :label="2" :disabled="radioType||selectStyle">样式3</el-radio>
            <el-radio :label="3" :disabled="radioType||selectStyle">样式4</el-radio>
            <el-radio :label="4" :disabled="radioType">样式5</el-radio>
            <el-radio :label="5" :disabled="radioType">样式6</el-radio>
            <el-radio :label="6" :disabled="radioType||selectStyle">样式7</el-radio>
            <el-radio :label="7" :disabled="radioType||selectStyle">样式8</el-radio>
          </el-radio-group>
          <el-input
            v-show="rightNowbtn"
            v-model="itemData.buyBtnName"
            @blur="btnPd"
            maxlength="4"
            placeholder="请输入按钮名称"
            :class="btnPdClass==true?'btnPdClass':''"
          ></el-input>
          <el-input
            v-show="rightAwaybtn"
            v-model="itemData.buyBtnName1"
            @blur="btnPd1"
            maxlength="4"
            placeholder="请输入按钮名称"
            :class="btnPdClass1==true?'btnPdClass':''"
          ></el-input>
        </el-form-item>
      </el-form>
    </div>
    <!-- 商品列表弹出框 -->
    <discountList :discountListShow="discountListShow" :discountId="itemData.activityId" :discountCommodityIds="itemData.discountCommodityIds" @discountClose="discountClose" @discountFun="discountFun"></discountList>
    <!-- 商品列表弹出框 -->
    <discountCommodity :discountCommodityShow="discountCommodityShow" :discountId="itemData.activityId" :discountCommodityIds="itemData.discountCommodityIds" @discountCommodityClose="discountCommodityClose" @discountCommodityFun="discountCommodityFun"></discountCommodity>
  </div>
</template>

<script>
import discountList from '@/components/popover/discountList';
import discountCommodity from '@/components/popover/discountCommodity';
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      trueDefault:true,
      btnPdClass: false,
      btnPdClass1: false,
      hoverIndex: -1, //hover的index
      loadingbut: false, //审核提交加载中
      loadingbuttext: "确 定",
      pageNumber: 5, //默认分页条数
      nowPage: 1, //默认第几页
      total: 1,
      tableData: [],
      selectData: [],
      search: {
        name: "",
        shopCategoryId: ""
      },
      search1: {
        name: "",
        shopCategoryId: ""
      },

      loading: false,
      dialogVisible: false,
      radioType: false,
      selectStyle: false,
      rightAwaybtn: false, //马上抢
      rightNowbtn: false, //立即购买
      dialogVisible1: false,
      hoverIndex1: -1, //hover的index
      shopId: "",
      getPost: true,
      // 弹出框数据
      discountListShow:false,
      // 商品弹出框
      discountCommodityShow:false,
    };
  },
  created() {
    this.shopId = Number(localStorage.getItem("shopId"));
  },
  mounted() {
    if (this.itemData.buyBtn == 3 || this.itemData.buyBtn == 7) this.rightNowbtn = true;
    if (this.itemData.buyBtn == 2 || this.itemData.buyBtn == 6) this.rightAwaybtn = true;
    this.radioType = !this.itemData.showBuyBtn
    // 回顯列表樣式
    if (this.itemData.listType == 2) {
      this.selectStyle = true;
    } else if (this.itemData.listType == 4) {
      this.selectStyle = true;
    } else {
      this.selectStyle = false;
    }
  },
  components: {
    discountList,
    discountCommodity
  },
  methods: {
    //展示样式
    showType(e) {
      switch (e) {
        case 0:
          if (this.itemData.chamfering == 0) {
            this.itemData.chamferingStyle = 0;
          } else {
            this.itemData.chamferingStyle = "8px";
          }
          break;
        case 1:
          if (this.itemData.projection == 0) {
            this.itemData.projectionStyle = "0px 0px 0px 0px";
          } else {
            this.itemData.projectionStyle =
              "0px 2px 2px 0px rgba(153,153,153,0.1)";
          }
          break;
        case 2:
          if (this.itemData.fill == 0) {
            this.itemData.fillStyle = 0;
          } else {
            this.itemData.fillStyle = "5px";
          }
          break;
      }
    },
    //多选框选中事件
    changeShow(e) {
      //当购买按钮被选中
      if (e == 0) {
        if (this.itemData.showBuyBtn == false) {
          this.radioType = true;
          this.itemData.buyBtn = -1;
          this.rightAwaybtn = false;
          this.rightNowbtn = false;
        } else {
          this.radioType = false;
          this.itemData.buyBtn = 0;
        }
      }
    },
    //列表样式变化
    listSelect() {
      if (this.itemData.listType == 2) {
        this.selectStyle = true;
        this.rightAwaybtn = false;
        this.rightNowbtn = false;

        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 1;
        } else {
          this.itemData.buyBtn = -1;
        }
      } else if (this.itemData.listType == 4) {
        this.selectStyle = true;
        this.rightAwaybtn = false;
        this.rightNowbtn = false;
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 1;
        } else {
          this.itemData.buyBtn = -1;
        }
      } else {
        this.selectStyle = false;
      }
      if (this.itemData.listType == 0) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 7;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
      if (this.itemData.listType == 1) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 4;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
      if (this.itemData.listType == 3) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 2;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
      if (this.itemData.listType == 5) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 6;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
    },
    //单选框变化
    selsecBtn() {
      // 如果buyBtn=2，6 是马上抢 buyBtnName1
      // 如果buyBtn=3，7 是立即购买 buyBtnName
      if (this.itemData.buyBtn == 2 || this.itemData.buyBtn == 6) {
        this.rightAwaybtn = true;
        this.rightNowbtn = false;
      } else if (this.itemData.buyBtn == 3 || this.itemData.buyBtn == 7) {
        this.rightNowbtn = true;
        this.rightAwaybtn = false;
      } else {
        this.rightAwaybtn = false;
        this.rightNowbtn = false;
      }
    },
    // 移入事件
    mouseOver(index) {
      this.hoverIndex = index;
    },
    // 移出事件
    mouseLeave() {
      this.hoverIndex = -1;
    },
    // 删除商品
    delGoods(index) {
      this.itemData.goodsData.splice(index, 1);
      this.itemData.discountCommodityIds.splice(index, 1);
    },
    // 移入事件
    mouseOver1() {
      this.hoverIndex1 = 1;
    },
    // 移出事件
    mouseLeave1() {
      this.hoverIndex1 = -1;
    },
    isChn(str) {
      if (!/^[\u4E00-\u9FA5]{2,4}$/.test(str)) {
        // alert("不全是中文");
        return false;
      } else {
        // alert("全是中文");
        return true;
      }
    },
    // 按钮输入框判断
    btnPd() {
      // console.log(this.itemData.buyBtnName.length);
      // console.log(this.isChn(this.itemData.buyBtnName));
      if (this.isChn(this.itemData.buyBtnName) == false) {
        this.$message.error("只能输入2到4个汉字");
        this.btnPdClass = true;
      } else {
        this.btnPdClass = false;
      }
    },
    btnPd1() {
      if (this.isChn(this.itemData.buyBtnName1) == false) {
        this.$message.error("只能输入2到4个汉字");
        this.btnPdClass1 = true;
      } else {
        this.btnPdClass1 = false;
      }
    },
    // 
    // 活动列表弹出框
    discountClose(e){
      this.discountListShow = false;
    },
    discountFun(e){
      console.log('父',e)
      this.discountListShow = false;
      this.itemData.goodsData = []
      this.itemData.discountCommodityIds = []
      this.itemData.activityId = e.id
      this.itemData.activityName = e.name
      this.itemData.activityStatus = e.activityStatus
      // 选择活动后生成倒计时
      clearInterval(this.itemData.timer)
      let timeObj = {}
      this.itemData.timer = setInterval(() => {
        let overTime = e.endTime.replace(/\-/g, "/");
        let leftTime = new Date(overTime) - new Date();//计算当前时间与活动结束时间的差值秒数
        let days = parseInt(leftTime/1000/60/60/24);
        let hours = parseInt(leftTime/1000/60/60%24);
        let minutes = parseInt(leftTime/1000/60%60);//计算剩余的分钟 
        let seconds = parseInt(leftTime/1000%60);//计算剩余的秒数
        if(days <=0 && hours <=0 && minutes <= 0 && seconds <= 0){
          clearInterval(this.itemData.timer);
          timeObj.d = 0
          timeObj.h = 0
          timeObj.m =0
          timeObj.s = 0
          timeObj.status = false;
          this.itemData.activityStatus = 2
        }else{
          timeObj.status = true;
          timeObj.d = (days <= 0) ? 0 : this.checkTime(days)
          timeObj.h = this.checkTime(hours)
          timeObj.m = this.checkTime(minutes)
          timeObj.s = this.checkTime(seconds)
        }
        this.itemData.timeObj = timeObj;
      }, 1000);
    },
    // 倒计时补零
    checkTime(i){  
      if(i<10) i = "0" + i; 
      return i; 
    },
    // 商品列表弹出框
    discountCommodityClose(e){
      this.discountCommodityShow = false;
    },
    discountCommodityFun(e){
      e.forEach( item => {
        item.accuracy = Number(item.accuracy)
        this.itemData.goodsData.push(item)
        this.itemData.discountCommodityIds.push(item.productId)
      })
      this.discountCommodityShow = false;
    },
  }
};
</script>
<style>
.add-dialog .el-dialog__body {
  padding: 0px 20px 50px 20px;
}
</style>
<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  padding: 20px 18px 24px 18px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.el-dialog__body {
  .pagination {
    padding-top: 20px;
  }
  .search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 20px;
    }
    .formBtnBox {
      margin-left: 20px;
    }
  }
  img {
    width: 100px;
    margin: 0 auto;
  }

  .bannerData {
    display: flex;
    flex-wrap: wrap;
    li {
      // width: 280px;
      box-sizing: border-box;
      padding: 6px;
      background-color: #f6f6f6;
      margin-right: 10px;
      margin-bottom: 20px;
      border: 1px solid #f6f6f6;
      cursor: pointer;
      transition: 0.2s;
      &:hover {
        background-color: #ecf5ff;
        border: 1px solid #a2cfff;
      }

      img {
        width: 260px;
        height: 166px;
        margin-bottom: 8px;
      }
      .sizes {
        color: #999;
      }
    }
    .clickLi {
      background-color: #ecf5ff;
      border: 1px solid rgba(0, 121, 253, 1);
    }
  }
}

.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .showType {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    .el-radio {
      width: 88px;
      margin: 0;
    }
    .showClass {
      display: inline-block;
      width: 52px;
      height: 24px;
      color: #999999;
      font-size: 12px;
      text-align: center;
      margin-right: 24px;
    }
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
    width: 126px;
    margin: 6px 0 16px 23px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    // color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 6px;
      width: 60px;
      margin-bottom: 10px;
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .goodsUploadList {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 68px;
      height: 68px;
      transition: 0.2s;
      cursor: pointer;
      border: 1px solid #ddd;
      box-sizing: border-box;
      padding: 4px;
      position: relative;
      margin-right: 5px;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%;
      }
      .delBtn {
        width: 16px;
        height: 16px;
        position: absolute;
        right: -8px;
        top: -8px;
        display: none;
      }
      .delBtnShow {
        display: block;
      }
    }
    li:hover {
      border: 1px solid #6babec;
    }
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
    cursor: pointer;
  }
  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }

  .buyStyle {
    display: flex;
    flex-wrap: wrap;
    .el-radio {
      width: 44px;
      margin: 6px 0 5px 25px;
    }
    .goodsUpload {
      margin: 7px 10px 0 27px;
    }
  }
  .customImg1 {
    width: 68px;
    height: 68px;
    transition: 0.2s;
    cursor: pointer;
    border: 1px solid #ddd;
    box-sizing: border-box;
    padding: 4px;
    position: relative;
    margin: 8px -14px 0 27px;
    img {
      width: 100%;
      height: 100%;
    }
    .delBtn {
      width: 16px;
      height: 16px;
      display: none;
      position: absolute;
      right: -8px;
      top: -8px;
      padding: 0;
    }
    .delBtnShow {
      display: block;
    }
    &:hover {
      border: 1px solid #6babec;
    }
  }
  .customImg {
    display: flex;
    align-items: center;
    cursor: pointer;
    .tips {
      font-size: 14px;
      width: 128px;
      line-height: 20px;
      color: #409eff;
    }
  }
}
.btnPdClass /deep/ .el-input__inner {
  border-color: #f56c6c;
}
</style>
