<template>
<!-- 

  getShopActivityListById    限时折扣活动列表 
  discountId:''   过滤掉该id的活动  不传不过滤
  

 -->
  <el-dialog :append-to-body="true" title="新增商品列表" :visible.sync="discountListShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
        <el-button style="float: right;margin-right: 15px" @click="openPage" size="small">新建活动</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="活动名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="活动名称" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="开始日期：">
            <el-date-picker
              v-model="searchList.beginTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder="选择开始时间"
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="结束日期：">
            <el-date-picker
              v-model="searchList.endTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder="选择结束时间"
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table
        ref="multipleTable"
        :data="list"
        tooltip-effect="dark"
        style="width: 100%">
        <el-table-column label="活动名称" prop="name"></el-table-column>
        <el-table-column label="活动时间">
          <template slot-scope="scope">
            {{scope.row.activityBeginTime + ' 至 ' + scope.row.activityEndTime}}
          </template>
        </el-table-column>
        <el-table-column label="活动状态" width="140">
          <template slot-scope="scope">
            <div style="color:#E6A23C;" v-if="scope.row.activityStatus == 0">未开始</div>
            <div style="color:#409EFF;" v-if="scope.row.activityStatus == 1">进行中</div>
            <div style="color:#F56C6C;" v-if="scope.row.activityStatus == 2">已结束</div>
            <div style="color:#F56C6C;" v-if="scope.row.activityStatus == 3">已失效</div>
          </template>
        </el-table-column>
        <el-table-column prop="productTotal" label="参与活动商品数" width="140"></el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="sub(scope.row)" type="text" size="small">选择</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getShopActivityListById } from '@/api/marketManage/discount'
export default {
  props: ["discountId","discountListShow"],
  watch:{
    discountListShow(newvalue,oldValue){
      if(newvalue){
        console.log('执行了')
        this.searchList.id = this.discountId || '';
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        pageNum:1,
        pageSize:5
      },
      list:[],
      listTotal:0,
      pickerOptions: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.searchList.activityType = 1
  },
  methods:{
    sub(e){
      console.log('e',e)
      this.$emit('discountFun',e)
    },
    openPage(){
      let details = this.$router.resolve({
        name: "新增限时折扣",
      });
      window.open(details.href, "_blank")
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.name = ''
      this.searchList.beginTime = ''
      this.searchList.endTime = ''
      this.getData();
    },
    // 列表 - 获取数据
    getData(){
      this.fromLoading = true
      console.log('请求的数据',this.searchList)
      getShopActivityListById(this.searchList).then(res=>{
        console.log('活动列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
        this.fromLoading = false
      })
      
    },
    close(){
      this.$emit('discountClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>